function Start({toggleVisible}){
    const [content, setContent] = useState([]);

    const nameRef = useRef();
    const surnameRef = useRef();

    useEffect(() => {
        setTimeout(() => {
            gsap.to(nameRef.current, {
                duration: 0.5,
                opacity: '0',
                ease: 'power4.out',
                delay: 0.2,
                onComplete: () => {
                    gsap.to(nameRef.current, {
                        duration: 0.5,
                        width: '1px',
                        ease: 'power4.out',
                        delay: 0.2,
                    });
                }
            });

            gsap.to(surnameRef.current, {
                duration: 0.5,
                opacity: '0',
                ease: 'power4.out',
                delay: 0.2,
                onComplete: () => {
                    gsap.to(surnameRef.current, {
                        duration: 0.5,
                        width: '1px',
                        ease: 'power4.out',
                        delay: 0.2,
                    });
                }
            });
        }, 1500);
    }, []);

    return(
        <div className="page page-start">
            <div className="page-start__content">
                <div className="main">
                    <span>яки<span className="mov" ref={nameRef}>мов</span></span><span>дан<span className="iil" ref={surnameRef}>иил</span></span>
                </div>
                <div className="sub">software developer and ITMO University student</div>
                <Link
                    className="go"
                    activeClass="active" to="about" spy={true} smooth={true} duration={500}
                    onClick={() => {
                        toggleVisible();
                    }}
                >
                    <i class="fas fa-angle-up"></i>
                </Link>
                <div className="page-start__links">
                    <a href="https://vk.com/yakidan" target="blank" className="link vk"></a>
                    <a href="https://github.com/yakidan" target="blank" className="link github"></a>
                </div>
            </div>
        </div>
    );
}

